* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
}

body {
  font-family: sans-serif;
  margin: 0 10px;
}

.bar-container-parent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 3rem;
  position: relative;
}
.bar-container-parent .sort-name {
  position: absolute;
  top: 0px;
  left: 0;
  font-size: 1.8rem;
  z-index: 100;
}
.bar-container-parent .bar-container {
  width: 47%;
  height: 80vh;
  display: flex;
  align-items: flex-end;
  border: 4px solid #555;
  overflow: hidden;
}
.bar-container-parent .bar-container.selected {
  border: 4px solid red;
}
.bar-container-parent .bar-container .bar {
  width: 1%;
  background-color: #add8ff;
  position: relative;
  border: 1px solid #888;
  border-width: 0 1px 0 0;
  cursor: pointer;
}
.bar-container-parent .bar-container .bar.red {
  background-color: #f45;
}
.bar-container-parent .bar-container .bar.yellow {
  background-color: #ff3;
}
.bar-container-parent .bar-container .bar.green {
  background-color: green;
}
.bar-container-parent .bar-container .bar::before {
  content: attr(data-value);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid #000;
  background-color: white;
  border-radius: 10%;
  z-index: 10;
  font-size: 1.5rem;
  padding: 5px;
  display: none;
  font-family: sans-serif;
  transition: display 0.2s ease-in-out;
}
.bar-container-parent .bar-container .bar:hover {
  background-color: #08a;
  border: 1px solid #333;
}
.bar-container-parent .bar-container .bar:hover::before {
  display: block;
}
.bar-container-parent .start-button {
  padding: 2px 5px;
  font-size: 1rem;
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 5px;
  transition: all 0.2s ease-in-out;
}
.bar-container-parent .start-button:hover:not(:disabled) {
  background-color: #000;
  color: #fff;
}

.buttons-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 25px 0;
}
.buttons-container > button {
  margin: 0 10px;
  padding: 10px 20px;
  cursor: pointer;
}
.buttons-container > input,
.buttons-container label {
  position: relative;
  margin: 0 10px;
}
.buttons-container > input::before {
  content: attr(value);
  position: absolute;
  left: 50%;
  top: -30px;
  padding: 5px;
  background-color: white;
  border: 1px solid #000;
  border-radius: 10%;
}

.result {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
  height: 100vh;
  overflow: hidden;
  padding: 0 2rem;
}
.result #restart {
  margin: 20px 0;
  padding: 15px 30px;
  cursor: pointer;
  font-size: 1.5rem;
  border: 2px solid #000;
  transition: all 0.2s ease-in-out;
}
.result #restart:hover {
  background-color: #000;
  color: #fff;
}
.result .chart {
  width: 600px;
  height: 600px;
  margin: 0 2rem;
}/*# sourceMappingURL=App.css.map */